	module.exports = {"data":{"markdownRemark":{"html":"<p>Desk PC after a long absence as a substitute for the <a href=\"/dynabook-sslx190\">main PC</a> breakdown. It is used for 25,800 yen and it is purchased with an exception value for performance. Just to have a head because of the lack of VGA performance that is often found in business PCs. Can you use it as an experiment PC for weight reduction of production if returning the opposite? Since the main PC has recovered in 2 months, it is active as a print server / file server and as a sub PC, for example when it can not catch up with one PC, such as low spec environment test and CD-R mass production. In May 2009, due to torrential rain during the move he passed away due to submersion. HDD could be salvaged, but it is painful to have lost one PC.</p>\n<h2>PC games that caught at the time</h2>\n<p>Toho things in general</p>\n<h2>Works / Other things I did</h2>\n<ul>\n<li>\n<p><a href=\"/!/thC/\">Toho Yumemiri Drama</a></p>\n<h2>Performance</h2>\n</li>\n<li>\n<p>CPU: intel Pentium 4 (Willamette / 1.9 GHz)</p>\n</li>\n<li>\n<p>RAM: 768 MB (PC - 3200)</p>\n</li>\n<li>\n<p>VGA: intel 845 GL (onBoard / VRAM 48 MB)</p>\n</li>\n<li>\n<p>HDD: 40 GB</p>\n</li>\n<li>\n<p>CDD: CD-RW</p>\n</li>\n<li>\n<p>FDD: 3.5 inch (3mode) × 1</p>\n</li>\n<li>\n<p>NET: 100Base-TX / 56kbps FaxModem</p>\n</li>\n<li>\n<p>SOUND: onBoard</p>\n</li>\n<li>\n<p>OS: Windows XP SP 2</p>\n</li>\n<li>\n<p>The latest specifications of the 2001 class</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-06-15T21:00:00+09:00","strDate":"2006/6/15","title":"(2006-2009) IBM NetVista M42Slim"}}},"pathContext":{"path":"/en/blog/2006/06/15-netvista-m42slim/","langKey":"en"}}

